@import "src/assets/sass/variables";

.aboutBox {
    z-index: 1000;
    position: absolute;
    width: 100%;
    height: 100%;
    overflow: hidden;
    overflow-y: auto;
    left: 0 !important;
    padding: 0;
    margin: 0;
    top: 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    background-color: $bgSecondary;
    color: white;
    .aboutWrapper {
        position: relative;
        display: flex;
        flex-direction: column;
        width: 100%;
        .aboutInner {
            display: flex;
            flex-direction: column;
            padding: 10px 24px;
        }
    }
}


